.Dialogs {
    display: grid;
    grid-template-columns: 4fr 10fr;
}

.Messages {
    border-left: 6px solid rgb(153, 207, 224);
    height: 700px;
    overflow-y: scroll;
}

/* .tab-Message {
    height: 700px;
} */

.tab-Message h1 {
    margin-left: 20px;
}



.dialog ul li  {
    margin-top: 20px;
    display: flex;
}
.dialog ul li img {
    width: 40px;
    height: 40px;
    list-style: none;
}
.dialog ul li a  {
    text-decoration: none;
    color: rgb(48, 29, 29);
    font-size: 20px;
    margin-top: 10px;
    margin-left: 10px;
}

.dialog ul li a:hover {
    font-weight: 600;
}


.message {
    margin-left: 50px;
    font-size: 20px;
}

.messag—É-sending_field {
    display: flex;
    /* position: initial;
    position: inherit;
    position: unset;
    position: static; */
    position: sticky;
    left: -500px;
    top: 675px;
    

}

.field-message-text {
    height: 70px;
    width: 550px;
    outline: none;
    display: block;
    font-size: 20px;
    padding-bottom: 20px;
    margin: auto;
    color: rgba(0, 0, 0, .5);
    border: 1px solid rgba(95, 196, 221, 0.836);
    background-color: rgba(96, 199, 218, 0.438);
    padding-left: 10px;
    z-index: 1;
}

.button-message {
    margin-top: 20px;
    margin-right: 25px;
    width: 150px;
    height: 54.5px;
    cursor: pointer;
    outline: none;
    border: 1px solid rgba(45, 199, 238, 0.9);
    background-color:rgba(45, 199, 238, 0.836);
    position: relative;
    top: -5.8px;
    color: #fff;
    left: 20px;
    font-size: 20px;
    font-family: 'Recursive', sans-serif;
}